<template>
    <div>
        <van-nav-bar
                title="每天一个好心情"
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
                class="clock_in"
        />
        <p class="van-nav-bar_title" style="text-align: center">{{title}}</p>
        <van-row>
            <van-col span="8" class="nav_title">{{author}}</van-col>
            <van-col span="8" class="nav_title">{{time}}</van-col>
            <van-col span="8" class="nav_title">浏览量：{{browse}}</van-col>
        </van-row>
        <div v-html="article_content" class="article_content"></div>
    </div>
</template>

<script>
    export default {
        name: "Detailsarticle",
        data(){
          return{
              id:'',
              title:'',
              author:'',
              time:'',
              browse:'',
              article_content:'',
              router:''
          }
        },
        methods:{
            onClickLeft(){
                this.router=this.$route.query.router
                if(this.router==='/person_article'){
                    this.$router.push('/person_article')
                }else {
                    this.$router.push({path:'/',query:{active:0}})
                }
            }
        },
        mounted() {
            this.id=this.$route.query.id
            this.$axios.get('https://zhuzuoji.com/myapi/blog/findblogbyblogid',{
                params:{
                    key:'vv9544',
                    blog_id:this.id
                }
            }).then(res=>{
                if(res.status==200&&res.data.state){
                    this.title=res.data.moments.news_title
                    this.author=res.data.moments.news_user_name
                    this.time=res.data.moments.news_time2.split(' ')[0]
                    this.browse=res.data.moments.news_view
                    this.article_content=res.data.moments.news_content
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        }
    }
</script>

<style scoped>
    .clock_in {
        background-color: rgb(146,188,255);
        position: fixed;
        width: 100%;
        height: 46px;
        top: 0;
    }
    .van-hairline--bottom:after{
        border: none;
    }
    .nav_title{
        text-align: center;
        font-size: 12px;
        color: #ccc;
    }
    >>>.van-nav-bar__title{
        color: #fff;
    }
    .article_content>>>img{
        width: 100px;
        /*display: block;*/
        /*text-align: center;*/
    }
    .article_content>>>pre{
        white-space: pre-wrap;
        word-wrap: break-word;
    }
    >>>.van-icon-arrow-left::before{
        color: #fff;
    }
    >>>.van-nav-bar__text{
        color: #fff;
    }
</style>